                              { required: true, message: '', trigger: 'blur' },
                              {  pattern: /^[0-9]*$/,
                                  message: '',
                                  trigger: 'blur'
                              }
                          ],
                   jingsaiYuyueDafen: [
                              { required: true, message: '', trigger: 'blur' },
                              {  pattern: /^[0-9]{0,6}(\.[0-9]{1,2})?$/,
                                  message: '6,2',
                                  trigger: 'blur'
                              }
                          ],
                   jingsaiYuyuePingshenText: [
